body {
    margin: 0;
    //background-color: #fcf4ec;
    background-color: #f7f6f2;
}

.home-section {
    margin-bottom: 10%;
    border: 3px solid chartreuse;

    .home-block {
        display: flex;
        flex-direction: row;
        //margin-bottom: 20%;
        
        .col-1 {
            width: 50%;

            .logo {
                font-family: 'EB Garamond', serif;
                font-size: 1.5rem;
                line-height: 136%;
                position: absolute;
                margin-left: 3%;
                color: #3a353f;
                z-index: 2;
            }

            .top-image {
                width: 30%;
                position: absolute;
                z-index: 2;
                margin-top: 10%;
                margin-left: 10%;
            }

            .buttom-image {
                width: 35%;
                position: absolute;
                z-index: 1;
            }    
        }
    
        .col-2 {
            width: 50%;

            nav {
                .menu {
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-end;
                    list-style-type: none;
                    margin: 5%;
                
                    a {
                        color: #1c325f;
                        text-decoration: none;
                        margin: 10px;
                        font-family: 'EB Garamond', serif;
                        font-size: 1.2rem;

                        &:hover {
                            border-bottom: 2px solid #1c325f;
                        }
                        &:active {
                            border-bottom: 2px solid #1c325f;
                        }
                    }
                }
            }
            
            .headline-text {

                h1 {
                    color: #1c325f;
                    font-family: 'EB Garamond', serif;
                    font-size: 7rem;
                    margin-top: 200px;
                    margin-bottom: 0;
                }
                h2 {
                    color: #1c325f;  
                    font-family: 'EB Garamond', serif;
                    font-size: 3rem;
                    margin: 0;
                } 
            }
        }
    }
}

.about-me {
    background-color: #c6d5c3;
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-top: 28%;

    border: 3px solid chartreuse;

    .textblock-col {
        width: 70%;
        margin-left: 5%;

        h1 {
            color: #6b7b65;
            font-family: 'EB Garamond', serif;
            font-size: 1.5rem;
            padding-top: 3%;
            text-transform: uppercase;
        }

        p {
            color: black;
            font-size: 2.5rem;
            font-family: 'EB Garamond', serif;
        }
        
        button {
            width: 15%;
            padding: 1.7%;
            margin-bottom: 3%;
            color: black;
            border: #ebac9f;
            font-family: 'EB Garamond', serif;
            font-size: 1.1rem;
            text-transform: uppercase;
            background-color: #ebac9f;
        }
    }
    /*
    .image-col {
        width: 60%;
        display: flex;
        justify-content: flex-end;
        
        img {
            width: 55%;
            //height: 100%;
            //margin-left: 202px;
        } 
    }
    */
}




// Mobile breakpoint
@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
    .home-section {
        border: 3px solid blue;
    }
}

// Tablet breakpoint
@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) {
    .home-section {
        border: 3px solid red;
    }
}
    